ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

li {
  font-size: 1.25rem;
}

.navBar {
  background-image: url(https://thumbs.dreamstime.com/b/watercolor-cat-paw-print-white-paper-background-texture-pattern-185923759.jpg);
  background-size: 15%;
  width: 100%;
  background-repeat: repeat;
  box-shadow: 4px 2px 5px gray;
  color: rgb(55, 195, 241);
  margin-bottom: 0.25rem;
  padding: 1.75rem 2rem;
}

.navBar button {
  background: rgb(55, 195, 241);
  border: 2px solid white;
  color: white;
  padding: 0.4rem 0.5rem;
}

.navBar button:hover {
  background: white;
  color: rgb(55, 195, 241);
  cursor: pointer;
}

.brand {
  cursor: pointer;
  font-size: 2rem;
}

